import Notiflix, { Loading } from "notiflix";
import React, { useEffect, useState } from "react";
import { Modal } from "react-bootstrap";
import { useMutation } from "@apollo/client";
import {
  currency,
  getLocalHouse,
  getStaffLogin,
  loadingScreen,
  messageError,
  messageSuccess,
  messageWarning,
  toDayDash,
} from "../../../../helper";
import _ from "lodash";
import { useFormik } from "formik";
import { v4 as uuidv4 } from "uuid";
import { UPDATE_EXTRA_EXPENSE } from "../apollo";
import {
  FormControl,
  InputAdornment,
  OutlinedInput,
  TextField,
} from "@mui/material";
import SelectExpressTypes from "../../../../helper/components/SelectExpressTypes";
import { s3Client } from "../../../../helper/s3Client";
export default function UpdateEx({ getData, onSuccess }) {
  const [show, setShow] = useState(false);
  const [upDateExtra] = useMutation(UPDATE_EXTRA_EXPENSE);
  const [userData, setUserData] = useState({});
  const [startDate, setStartDate] = useState(toDayDash());
  const [imageName, setImageName] = useState("");
  const [file, setFile] = useState(null);

  const [getTypeExpress, setTypExpress] = useState({});
  const getUser = userData?.firstName + " " + userData?.lastName;
  const [localHouse, setLocalHouse] = useState("");
  // state price
  const [inputPrice, setInputPrice] = useState();
  const [defaultPrice, setDefaultPrice] = useState();

  useEffect(() => {
    setLocalHouse(getLocalHouse()?._id);
  }, []);
  useEffect(() => {
    const local = getStaffLogin();
    setUserData(local?.data);
  }, []);
  const handleUpload = async (event) => {
    const imageName = uuidv4() + "." + event.target.files[0].type.split("/")[1];
    const _file = event.target.files[0];
    setFile(_file);
    const res = await s3Client.uploadFile(_file, imageName);
    if (res?.location) {
      setFile(_file);
      setImageName(imageName);
      messageSuccess("ອັບໂຫຼດເອກະສານສຳເລັດແລ້ວ");
    } else {
      setFile(null);
      setImageName("");
      messageWarning("ການອັບໂຫຼດເອກະສານບໍ່ສຳເລັດ");
    }
  };

  // qurey total
  const _dataEndBalanceKIP = getData?.endBalanceKIP;
  // get price input
  const finalTPrice = parseInt(_dataEndBalanceKIP) + parseInt(defaultPrice);
  // plus total
  const getFinalTotalPrice = parseInt(finalTPrice) - parseInt(inputPrice);
  const {
    handleChange,
    errors,
    values,
    handleSubmit,
    resetForm,
    setFieldValue,
    submitCount,
  } = useFormik({
    initialValues: {
      detail: "",
      expenseKIP: "",
    },
    enableReinitialize: false,
    validate: (values) => {
      const errors = {};
      if (!values.expenseKIP) {
        errors.expenseKIP = "ກະລູນາປ້ອນຈຳນວນເງິນ";
      }
      if (!values.detail) {
        errors.detail = "ກະລູນາປ້ອນລາຍລະອຽດ";
      }
      if (!getTypeExpress?.id_expense)
        errors.expenseType = "ກະລູນາເລືອກປະເພດລາຍຈ່າຍ";
      return errors;
    },
    onSubmit: async (values) => {
      loadingScreen();
      try {
        const { data: _create } = await upDateExtra({
          variables: {
            data: {
              accountantDate: String(startDate),
              fileUpload: String(imageName),
              StaffFullName: String(getUser),
              house: String(localHouse),
              detail: String(values.detail),
              extraType: "EXPENSE",
              expenseKIP: parseInt(inputPrice),
              endBalanceKIP: parseInt(getFinalTotalPrice),
              expenseType: String(getTypeExpress?.id_expense),
            },
            where: {
              id_list: parseInt(getData?.id_list),
            },
          },
        });
        if (_create) {
          Notiflix.Loading.remove();
          messageSuccess("ລົງລາຍຈ່າຍສຳເລັດແລ້ວ");
          onSuccess();
          setImageName("");
          setTimeout(() => {
            resetForm({ values: "" });
            window.scrollTo(0, 0);
          }, 100);
          setShow(false);
        } else {
          messageError("ລົງລາຍຈ່າຍບໍສຳເລັດ");
          onSuccess();
        }
      } catch (error) {
        messageError("ການເພີ່ມຂໍ້ມູນຜິດພາດ");
        console.log(error);
        onSuccess();
      }
    },
  });
  useEffect(() => {
    if (!show) {
      return;
    }
    setTypExpress(getData?.expenseType);
    setFieldValue(
      "accountantDate",
      getData?.accountantDate ? getData?.accountantDate : "",
      false
    );
    setFieldValue("detail", getData?.detail ? getData?.detail : "", false);
    setInputPrice(getData?.expenseKIP ? getData?.expenseKIP : 0);
    setDefaultPrice(getData?.expenseKIP ? getData?.expenseKIP : 0);
  }, [getData, show]);

  return (
    <>
      <button
        className="btn btn-sm"
        onClick={() => setShow(true)}
        style={{ marginTop: -1 }}
      >
        <i className="fas fa-edit"></i>
      </button>

      <Modal show={show} onHide={() => setShow(false)} size="lg">
        <Modal.Header>
          <h3>ຟອມແກ້ໄຂລົງລາຍຈ່າຍ</h3>
          <a
            className="pull-right float-end "
            style={{ textDecoration: "none", marginTop: -10 }}
            onClick={() => setShow(false)}
          >
            <i className="icon-x fa-2x text-danger" />
          </a>
        </Modal.Header>
        <div className="p-2" style={{ backgroundColor: "#f2f7f3" }}>
          <div className="form-row">
            <div className="col-md-12">
              <TextField
                label=" "
                variant="outlined"
                name="fileUpload"
                onChange={handleUpload}
                type="file"
                accept="image/x-png,image/gif,image/jpeg"
                sx={{
                  m: 0,
                  width: "100%",
                  backgroundColor: "#ffff",
                }}
              />
            </div>
          </div>
          <div className="form-row mt-1">
            <div className="col-md-12">
              <SelectExpressTypes
                style={{ minWidth: 200 }}
                value={getTypeExpress?.id_expense}
                onChange={(obj) => {
                  if (obj?.id_expense) {
                    setTypExpress(obj);
                  }
                }}
              />
              <div className="text-danger">{errors.expenseType}</div>
            </div>
          </div>

          <div className="form-row mt-1">
            <div className="col-md-12">
              <FormControl fullWidth sx={{ m: 0, fontSize: 20 }}>
                <OutlinedInput
                  startAdornment={
                    <InputAdornment position="start">
                      ຍອດຄົງເຫຼືອ:{" "}
                    </InputAdornment>
                  }
                  type="text"
                  placeholder="ລາຍຈ່າຍ"
                  disabled={true}
                  value={currency(_dataEndBalanceKIP)}
                />
              </FormControl>
              <div className="text-danger">{errors.expenseKIP}</div>
            </div>
          </div>

          <div className="form-row mt-1">
            <div className="col-md-12">
              <FormControl fullWidth sx={{ m: 0 }}>
                <OutlinedInput
                  startAdornment={
                    <InputAdornment position="start">
                      ຈຳນວນເງິນ:{" "}
                    </InputAdornment>
                  }
                  type="number"
                  sx={{
                    backgroundColor: "#ffff",
                  }}
                  placeholder="ລາຍຈ່າຍ"
                  onWheel={(e) => e.target.blur()}
                  value={inputPrice}
                  name="expenseKIP"
                  onChange={(e) => {
                    setInputPrice(e.target.value);
                  }}
                />
              </FormControl>
              <div className="text-danger">{errors.expenseKIP}</div>
            </div>
          </div>
          <div className="form-row mt-1">
            <div className="col-md-12">
              <FormControl fullWidth sx={{ m: 0 }}>
                <OutlinedInput
                  startAdornment={
                    <InputAdornment position="start">ວັນທີ່: </InputAdornment>
                  }
                  type="date"
                  placeholder="ລາຍຈ່າຍ"
                  name="accountantDate"
                  sx={{
                    backgroundColor: "#ffff",
                  }}
                  value={startDate}
                  onChange={(e) => setStartDate(e.target.value)}
                />
              </FormControl>
            </div>
            <label className="text-black mt-1">ລາຍລະອຽດ</label>
            <div className="col-md-12">
              <textarea
                rows={3}
                name="detail"
                className={"form-control form-control-lg"}
                placeholder="..."
                onChange={handleChange}
                value={values?.detail}
              ></textarea>
              <div className="text-danger">{errors.detail}</div>
            </div>
          </div>
        </div>
        <Modal.Footer className="border-top">
          <button
            type="button"
            className="btn btn-primary btn-block btn-lg"
            onClick={() => handleSubmit()}
          >
            <i className="icon-save" style={{ marginRight: 3 }} />
            ບັນທຶກ
          </button>
        </Modal.Footer>
      </Modal>
    </>
  );
}
